import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import banner from '@/components/common-components/bannerSlide.vue';
let AppList = class AppList extends Vue {
    constructor() {
        super(...arguments);
        this.sessionId = '';
        this.appList = [
            {
                title: '基础配置',
                list: [
                    {
                        icon: 'iconshangpin',
                        label: '公告配置',
                        path: '/schoolAdmin/noticeConfig'
                    }
                ]
            },
            {
                title: '接单配置',
                list: [
                    {
                        icon: 'iconshangpin',
                        label: '基础价格',
                        path: '/schoolAdmin/priceConfig'
                    },
                    {
                        icon: 'iconshangpin',
                        label: '发薪方案',
                        path: '/schoolAdmin/payoffConfig'
                    },
                    {
                        icon: 'iconshangpin',
                        label: '业务配置',
                        path: '/schoolAdmin/businessConfig'
                    }
                ]
            },
            {
                title: '商家管理',
                list: [
                    {
                        icon: 'iconshangpin',
                        label: '商家列表',
                        path: '/schoolAdmin/businessManagement'
                    }
                ]
            },
            {
                title: '推广宣传',
                list: [
                    {
                        icon: 'iconshangpin',
                        label: '项目码',
                        path: '/schoolAdmin/itemCode'
                    }
                ]
            }
        ];
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    mounted() {
        this.sessionId = this.$route.query.sessionId;
    }
    appClick(item) {
        if (item.path) {
            this.$router.push({ path: item.path, query: { isTo: 'true' } });
        }
    }
};
AppList = __decorate([
    Component({
        components: {
            banner
        }
    })
], AppList);
export default AppList;
